@import url(https://fonts.googleapis.com/css2?family=Viga&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App {
  text-align: center;
  font-family: 'Viga', sans-serif;
}


.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 25px;

}

.App-link {
  color: #61dafb;
}



.square {
	height: 50px;
	border: 1px solid white;
	width: calc(25% - 2px);
  margin-left: 1px;
  margin-top: 1px;
  margin-bottom: 1px;
  margin-right: 1px;
  text-align: center;
  color: black;
  font-size: 18px;
}

.button {
	display:flex;
	flex-wrap:wrap;
	margin: 0 auto;
	width: 25%;
	height:25%;
	background: grey;
	
}

.operator {
	background: #dd7631;
	color: white;
}

.number {
	background: #b1b493;
}
.input {
	height:10vh;
	width: 25%;
	background: black;
	color: white;
	justify-content: center;
	align-items: center;
	margin: 0 auto;
	padding-top: 20px;
	font-size: 24px;
}
